.exhibitionDesc {
  -webkit-line-clamp: 2;
  /* stylelint-disable-next-line property-no-unknown */
  line-clamp: 2;
  padding-right: 24px;
  margin: 10px 0;
  max-width: 655px;
}

@media (--mq-is-mobile) {
  .exhibitionDesc {
    -webkit-line-clamp: 4;
    margin: 8px 0 16px;
    max-width: unset;
  }
}
