.profileBody {
  composes: bannerRoot from 'css/pages/profiles/banner/Banner.module.css';
  width: 100%;
  padding: 0px var(--profile-pagePadding);
  box-sizing: border-box;
  position: relative;
  margin-top: var(--bannerHeight);
  z-index: 10;
}
.profilePageContent {
  background-color: var(--color-commonWhite);
  --profile-pagePadding: max(32px, calc((100vw - 1920px) / 2 ));
}

.subpageContent {
  margin: 0px calc(-1 * var(--profile-pagePadding));
  padding: 0px var(--profile-pagePadding);
  background-color: var(--color-backgroundDefault);
}

.mobileFooterSpacer {
  height: 55px;
}


@media (--mq-is-desktop) {
  .profilePageContent {
    --profile-pagePadding: max(32px, calc((100vw - 1440px) / 2 ));
  }
}

@media (--mq-is-mobile) {
  .profileBody {
    margin-top: var(--bannerHeight);
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
  }

  .profilePageContent {
    --profile-pagePadding: 24px;
    height: calc(100vh - 63px);
    overflow-y: auto;
    overflow-y: overlay;
    position: relative;
    display: flex;
    flex-direction: column;
  }

  .profilePageFooter {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 55px;
    padding: 0 16px;
    background-color: rgba(255, 255, 255, 0.79);
    box-shadow: 0px 0px 11px 8px rgba(0, 0, 0, 0.04);
    backdrop-filter: blur(25px);
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 200;
  }

  .subpageContent {
    flex: 1 1 auto;
  }
}
