.insights {
  justify-content: flex-end;
  padding-bottom: 12px;
  gap: 22px;
}

.insight {
  border: 0;
}

@media (--mq-is-mobile) {
  div.insights {
    flex-direction: column;
    width: 100%;
    gap: 8px;
  }
  div.insight {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
