.idBaseHeaderContainer {
  composes: bannerRoot from 'css/pages/profiles/banner/Banner.module.css';
  position: relative;
  background-color: var(--color-commonWhite);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: auto;
  margin-top: 0;
  padding-top: calc(var(--bannerHeight));
  height: auto;
  width: 100%;
  align-items: center;
  overflow: hidden;
}

.idBaseHeaderContainer.collection {
  padding-top: 0;
}

@media (--mq-is-mobile) {
  .idBaseHeaderContainer {
    flex: 0 0 0;
  }
}
