.EditProfileDialog {
  composes: bannerRoot from 'css/pages/profiles/banner/Banner.module.css';
  --maxDialogHeight: 100vh;
}

.ProfileContainer {
  width: 100%;
  padding: 0;
  box-sizing: border-box;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: calc(var(--bannerHeight));
  margin-top: 8px;
}

.profileImageContainer {
  margin: 24px 0;
}

.profilePhotoWrapper {
  width: 68px;
  height: 68px;
  border-radius: 50%;
  border: 0;
  z-index: 5;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  pointer-events: all;
}

.profilePhoto {
  position: absolute;
  top: 0;
  left: 0;
  width: 68px;
  height: 68px;
  border-radius: 50%;
  border: 0;
  object-fit: cover;
}

.profilePhotoWrapper::after {
  background-color: var(--color-backgroundOverlay);
  content: ' ';
  position: absolute;
  width: 100%;
  height: 100%;

}

.hoverOverlay {
  cursor: pointer;
  width: 100%;
  height: 100%;
}

.hoverOverlayMaskContainer, .hoverOverlay {
  background-color: var(--color-backgroundOverlay);
}

.hoverOverlayMaskContainer {
  overflow: hidden;
  position: absolute;
  padding-top: calc(100% / var(--aspectRatio) );
  width: 100%;
  top: 0;
  left: 0;
}

.hoverOverlayMask {
  margin-top: calc(-1 * var(--bannerHeight));
  background-color: var(--color-backgroundOverlay);
}


.addImageIconWrapper {
  background-color: transparent;
  width: 32px;
  height: 32px;
  border-radius: 0;
  left: calc(50% - 16px);
  top: calc( 50% - 16px);
  position: absolute;
  z-index: 5;
  flex: 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  flex-direction: column;
}

.addImageIconWrapper > .addImageIcon {
  width: 25px;
  height: 25px;
}

.addImageIconWrapper.banner {
  width: 32px;
  height: 32px;
  left: calc(50% - 16px);
  top: calc( 50% - 16px);
}

.addImageIconWrapper.banner > .addImageIcon {
  width: 40px;
  height: 40px;
  outline: none;
}

.dialogPadding {
  padding: 24px 34px 20px;
  box-sizing: border-box;
  position: relative;
}

.socialMediaPrefix {
  margin-right: -7px;
  cursor: default;
}

.socialMediaInputLabel {
  display: flex;
  align-items: center;
}

.infoIcon {
    cursor: pointer;
}

.socialMediaTextField {
  margin: 0 0 6px;
}

@media (--mq-is-not-mobile) {
  .EditProfileDialog {
    --maxDialogHeight: 90vh;
  }
}

@media (--mq-is-mobile) {
  .dialogPadding {
    padding: 24px 24px 20px;
  }

  .profilePhoto, .profilePhotoWrapper {
    width: 68px;
    height: 68px;
  }
}
