.dialog {
  --maxDialogHeight: 100vh;
}

@media (--mq-is-not-mobile) {
  .dialog {
    --maxDialogHeight: 90vh;
  }
}

.scrollContainer {
  display: flex;
  flex-direction: column;
}

.followUserRow {
  display: flex;
  gap: 12px;
  align-items: center;
  justify-content: space-between;
  margin: 8px 12px;
}

.followUserLink {
  display: flex;
  align-items: center;
  gap: 8px;
  min-width: 0;
}

.profileImage {
  width: 36px;
  height: 36px;
  border-radius: 9999px;
  z-index: 5;
}

.followButton {
  width: 87px;
}